<template>
  <div class="jianli-container clearfix">
    <scrollbar class="jianli-scroll" :settings="settings" @ps-y-reach-end="scrollHanle">
      <div class="jianli-wrap clearfix">
        <ul class="list-title clearfix">
          <li class="">个人简历</li>
          <li class=""></li>
          <li class=""></li>
        </ul>
        <!--边框-->
        <div class="list-title-border clearfix">
          <div class="list-border-left">
            <div class="item-left"></div>
            <div class="item-right"></div>
          </div>
          <div class="list-border-right">
            <div class="item-left"></div>
            <div class="item-right"></div>
          </div>
        </div>
        <div class="list-content clearfix">
          <div class="list-content-wrap clearfix">
            <div class="item-default-wrap clearfix" v-for="(user,index) in userList">
              <div class="item-mark clearfix">
                <div class="item-left"></div>
                <div class="item-right"></div>
                <div class="item-bottom"></div>
              </div>
              <div class="item-name">{{user.title}}</div>
              <!--基本信息 技能状况 工作经历 项目经历 自我评价-->
              <div v-if="user.title == '基本信息'"class="item-user base">
                <div class="item-left">
                  <ul>
                    <template v-for="(base, baseIndex) in user.userBase">
                      <li class="base-default">{{base.property}}<span>:</span>{{base.text}}</li>
                    </template>
                  </ul>
                </div>
                <div class="item-right">
                  <div class="item-user-img">
                    <img src="../assets/images/user.jpg" alt="">
                  </div>
                </div>
              </div>
              <div v-if="user.title == '技能状况'" class="item-user skill">
                {{user.beforeText}}<br>
                {{user.nowText}}<br>
                {{user.animate}}<br>
              </div>
              <div v-if="user.title == '工作经历'" class="item-user skill">
                <template v-for="(work,wordIndex) in user.workList">
                  {{work.time}}<br>
                  {{work.unit}}
                  {{work.text}}<br>
                </template>

              </div>
              <div v-if="user.title == '项目经历'" class="item-user skill">
                <template v-for="(project,wordIndex) in user.projectList">
                  {{project.projectName}}<br>
                  {{project.url}}<br>
                  {{project.text}}<br>
                </template>
              </div>
              <div v-if="user.title == '自我评价'" class="item-user skill">
                  <div style="text-indent: 2em">本人性格开朗积极进取,适应力强、上手快、勤奋好学、脚踏实地有较强的团队精神,工作积极进取,态度认真。</div>
                  <div style="text-indent: 2em">忠实诚信,讲原则，说到做到，决不推卸责任;有自制力，做事情始终坚持有始有终，从不半途而废;有问题不逃避,愿意虚心向他人请教。</div>
                  <div style="text-indent: 2em">自从步入社会，我才懂得了 “机会不上门来找人，只有人去找机会”，怀着饱满的热情、坚韧的性格、勤奋的态度，抓住这个机会，我会为单位尽自己绵薄之力。</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </scrollbar>
  </div>
</template>
<script>
  // 滚动条插件
  import scrollbar from 'vue-perfect-scrollbar/index.vue'
  export default {
    data() {
      return {
        // 滚动条配置
        settings: {
          maxScrollbarLength: 300
        },
        userList: [
          {
            title: '基本信息',
            userBase: [
              {
                property: '姓名',
                text: '孙伟'
              },
              {
                property: '出生年月',
                text: '1992年7月18日'
              },
              {
                property: '民族',
                text: '汉族'
              },
              {
                property: '家庭住址',
                text: '辽宁省铁岭市'
              },
              {
                property: '电话',
                text: '18641097682'
              },
              {
                property: '毕业院校',
                text: '辽宁科技大学'
              },
              {
                property: '邮箱',
                text: '18841084056@163.com'
              },
              {
                property: '个人主页',
                text: 'sunshinewith.top'
              },
              {
                property: 'qq',
                text: '545212259'
              },
            ]
          },
          {
            title: '技能状况',
            beforeText: '以前项目 主要使用 jq + bootstrap + sass',
            nowText: '目前项目 主要使用vue + es6 + scss + element-ui + echart + ueditor 以及vue的 一系列插件',
            animate: '还使用canvas 拖拽 实现一些功能 对混合开发有一定的了解'
          },
          {
            title: '工作经历',
            workList: [
              {
                time: '2015.7  ~~~ 2016.3',
                unit: '大连泰山热电站',
                text: '设备维护与维修'
              },
              {
                time: '2016.4  ~~~ 20018.7',
                unit: '大连爱丁云教育科技有限公司承远分公司',
                text: 'web前端开发'
              }
            ]
          },
          {
            title: '项目经历',
            projectList: [
              {
                projectName: '易学社考试平台',
                url: 'http://yun.edustudy.cc:8080/eduExamCenter',
                text: '帐号:18800000000 密码:123456'
              },
              {
                projectName: '己简教学微课平台(类似于MOOC)',
                url: 'http://yun.edustudy.cc:8080/eduExam',
                text: '帐号:13300000000 密码:123456'
              },
              {
                projectName: '己简课堂',
                url: 'http://yun.edustudy.cc:8882',
                text: '帐号:18640821002 密码:123456'
              }
            ]
          },
          {
            title: '自我评价',
          },
        ]
      }
    },
    components: {
      scrollbar
    },
    methods: {
      // 滚动条
      scrollHanle(evt) {
      },
    }
  }
</script>
<style scoped>
  .jianli-scroll {
    position: relative;
    margin: auto;
    width: 100%;
    height: 100%;
  }
</style>



